// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import Loading from 'components/lib/loading'
import CenterContainer from 'components/lib/center-container'
import axios from 'axios'


if (process.env.NODE_ENV === 'development' ){
   axios.defaults.baseURL = 'http://localhost:8888/laracappo/public/api';
}
else{
   axios.defaults.baseURL = 'https://aws.techthatinterest.com/api';
}
if (axios.defaults.headers.common['Authorization'] == undefined){
   var theToken = localStorage.getItem('user-token');
   axios.defaults.headers.common['Authorization'] = theToken
}

Vue.config.productionTip = false

Vue.component('loading', Loading)
Vue.component('center-container', CenterContainer)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App },
})



