<template>
      <div class="cards">
            <h2>Articles</h2>
            <button type="button" class="btn btn-primary mb-2" @click="clearArticle()">Add</button>
   
              <form @submit.prevent="addArticle" >
              <div class="modal modal-fullscreen" id="modalLarge" tabindex="-1" role="dialog" aria-labelledby="modalLargeLabel" aria-hidden="true">
                  
                  <div class="modal-dialog" role="document">
                        
                      <div class="modal-content">
                          <div class="modal-header">
                              <h5 class="modal-title" type="text" id="modalLargeLabel"><input v-model="article.title" placeholder="Title"></h5>
                              <button @click="stopEditing" class="close" aria-label="Close">
                                  <span aria-hidden="true">&times;</span>
                              </button>
   
                          </div>
                              <div class="input-group mb-3">
                                 <div class="input-group-prepend">
                                 <span class="input-group-text" id="basic-addon1">@</span>
                                 </div>
                                 <input type="text" v-model="article.label" class="form-control" placeholder="Label" aria-label="Label" aria-describedby="basic-addon1">
                              </div>
                              <textarea type="text" id="largetextarea"class="form-control" rows="10" v-model="article.body" placeholder="Body"></textarea>
                          
                          <div class="modal-footer">
                              <!-- <button @click="stopEditing" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button> -->
   
                              <button type="submit" class="btn btn-secondary" aria-hidden="true" data-toggle="modal" data-target="#modalLarge" >Save</button>
   
                             
                          </div>
                      </div>
                     
                  </div>
                 
              </div>
            </form>
   
                <h3> Search </h3>
               <input type="text" v-model= "search" class="form-control mb-4" v-bind:placeholder="'Search in ' + selectedlabel">
             
            <nav aria-label="Page navigation example">
                  <ul class="pagination">
                    <li v-bind:class="[{disabled: !pagination.prev_page_url}]" class="page-item"><a class="page-link" href="#" @click="fetchArticles(pagination.prev_page_url)">Previous</a></li>
                     <li class="page-item disabled"><a class="page-link text-dark">Page {{ pagination.current_page }} of {{ pagination.last_page}}</a></li>
                    <li v-bind:class="[{disabled: !pagination.next_page_url}]" class="page-item"><a class="page-link" href="#" @click="fetchArticles(pagination.next_page_url)">Next</a></li>
                  </ul>
                </nav>
         
            
   
            <div class="row">
                  <div class="col-md-3" v-for="article in filteredArticles" @click="editArticle(article)"   v-bind:key="article.id">
              
                    <div class="card mb-3 box-shadow">
                           <div class="card-body">
                                 <h5 class="card-title">{{ article.title }}</h5>
                              <p class="card-text">{{ article.sbody }}</p>
                              <div class="d-flex justify-content-between align-items-center">
                              
                              <small class="text-muted">{{ article.label }}</small>
                              <div class="btn-group">
                                    <!-- <button type="button" class="btn btn-sm btn-outline-secondary">View</button> -->
                                    <button type="button" class="btn btn-sm btn-outline-secondary" v-on:click.stop @click="deleteArticle(article.id)" id="deleteButton" >Delete</button>
                                 </div>
                              </div>
                           </div>
                     </div>
   
                     <!-- <div class="card mb-4" >
                           <div class="card-body">
                             <h5 class="card-title">{{ article.title }}</h5>
                             <h6 class="card-subtitle mb-2 text-muted">{{ article.label }}</h6>
                             <p class="card-text">{{ article.sbody }}</p>
                             <a href="#" class="card-link">Card link</a>
                             <a href="#" class="card-link">Another link</a>
                           </div>
                         </div> -->
   
   
                  </div>
            </div>
      </div>

</template>

<script>
  

   import axios from 'axios';
   import {AUTH_LOGOUT} from 'actions/auth'
      export default {
        name: 'cards',
        data(){
            return{
               articles: [],
               sarticles: [], // This is used for shortened articles
               article: {
                  id: '',
                  title: '',
                  body: '',
                  label: ''
               },
               article_id: '',
               pagination: {},
               edit: false,
               search: "",
               current_page_url: "",
               // currentlabel: "",
               selectedlabel: ""
               }
               
         },
         mounted(){
           
            let vm = this;
             this.$root.$on('thelabel', function (msg) {
                  vm.selectedlabel = msg;
                  // console.log(this.selectedlabel);
                  vm.article.label = msg;
                  vm.fetchArticles();
               })

             


         },
         created() {
                  this.fetchArticles();

                  //  function isMobileDevice() {
                  //       return (typeof window.orientation !== "undefined") || (navigator.userAgent.indexOf('IEMobile') !== -1);
                  //    };
                  //    $(".showingtime").hide("slow");
                     // if(isMobileDevice() == false){
                     //    // $(".showingtime").show();
                     // }
         },
         
      methods: {
         stopEditing(){
            this.edit = true;
            $('#modalLarge').modal('hide');
            $('body').removeClass('modal-open');
            $('.modal-backdrop').remove();
            
         },
         fetchArticles(page_url){
            // console.log(this.selectedlabel);
            var thelabel = "";
            if (this.selectedlabel != ""){
               thelabel = '/articles/'+this.selectedlabel;
            }
            else{
               thelabel = '/articles';
            }
            // console.log(thelabel);

            // This indicates that its querying all labels. This is to make sure no user created this one.
            if (thelabel === "/articles/All Labels"){
               thelabel = '/articles';
            }
            
            
            this.current_page_url = page_url;
            let vm = this;
            page_url = page_url || thelabel;
            // console.log(page_url);
            axios
            .get(page_url)
            
            .then(res => {
               var resp = res.data;

               // console.log(resp.data);
               
               this.articles = resp.data;
             
   
               vm.makePagination(resp.meta, resp.links);
            })
            .catch(err => console.log(err));
         },

         makePagination(meta, links) {
            let pagination = {
               current_page: meta.current_page,
               last_page: meta.last_page,
               next_page_url: links.next,
               prev_page_url: links.prev
            }

             this.pagination = pagination;
         },

         deleteArticle(id){

            if(confirm('Are you Sure?')){
              
               axios.delete(`article/${id}`)
               .then(data => {
                  this.fetchArticles(this.current_page_url);
                  
               })
               .catch(err => console.log(err));
            }
         },
         clearArticle(){
            $('#modalLarge').modal('show');
            this.edit=false;
            this.article.id = '';
            this.article.article_id = '';
            this.article.title = '';
            this.article.body = '';
         },

         addArticle() {
            // console.log("Came to add article");
            
            if (this.article.body=="" && this.article.title==""){
               $('#modalLarge').modal('hide');
                  $('body').removeClass('modal-open');
                  $('.modal-backdrop').remove();
               return;
            }
            
            
            
            if (this.edit == false){
               this.search = "";
               
               // Add
               axios.post('/article',{
                  title: this.article.title,
                  body: this.article.body,
                  label: this.article.label
                  
               })
               // .then(res =>res.json())
               .then(data =>{

                  // console.log(data);
                  
                  $('#modalLarge').modal('hide');
                  $('body').removeClass('modal-open');
                  $('.modal-backdrop').remove();
                  this.article.title='';
                  this.article.body='';
                  // alert('Article Added');
                  this.fetchArticles();
                  this.$root.$emit('refreshlabel');

                  
               })
               .catch(err => console.log(err));
               
            }
            else{
               // Update
               // console.log("Came here");
               
               axios.put('/article',{
                  article_id: this.article.id,
                  title: this.article.title,
                  body: this.article.body,
                  label: this.article.label
               })
               .then(data =>{
                  
                  $('.modal-backdrop').remove();
                  this.article.title='';
                  this.article.body='';
                  // alert('Article Updated');
                  $('#modalLarge').modal('hide');
                  $('body').removeClass('modal-open');
                  $('.modal-backdrop').remove();
                  this.fetchArticles(this.current_page_url);
               })
               .catch(err => console.log(err));
               this.edit = false;
               this.article.id = '';
               this.article.article_id = '';
               this.article.title = '';
               this.article.body = '';
               

            }
            
         },
         

         editArticle(article) {
            $('#modalLarge').modal('show');
            this.edit = true;
            this.article.id = article.id;
            this.article.article_id = article.id;
            this.article.title = article.title;
            this.article.body = article.body;
            this.article.label = article.label;
         },
         
      },
      computed: {
            filteredArticles:function(){
               return this.articles.filter((article) =>{
                  // console.log(this.search);
                  return ((article.title).toLowerCase()).match(((this.search).toLowerCase())) || ((article.body).toLowerCase()).match(((this.search).toLowerCase()));
               });
            }
         }
      }
</script>

<style>
   #largetextarea{
   resize: 100%;
  outline: none;
  width: 100%;
  border: none;
  height: 100%;

}
@media (max-width: 767px) {
   .modal.modal-fullscreen, .bootstrap-fs-modal .modal {
     box-shadow: 0px 0px 0px 100px white;
   }
   .modal.modal-fullscreen .modal-dialog, .bootstrap-fs-modal .modal .modal-dialog {
     margin: 0;
     height: 100%;
     max-width: 100%;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content, .bootstrap-fs-modal .modal .modal-dialog .modal-content {
     border-style: none;
     border-radius: 0;
     height: 100%;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-header, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-header {
     justify-content: flex-start;
     min-height: 50px;
     line-height: 50px;
     padding: 0;
     background-color:white;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-header .modal-title, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-header .modal-title {
     white-space: nowrap;
     line-height: 50px;
     font-size: 1.1rem;
     padding-bottom: 10px;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-header .close, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-header .close {
     float: none;
     order: -1;
     margin: 0;
     padding: 0;
     margin-right: 12px;
     height: 50px;
     color: transparent;
     text-shadow: none;
     opacity: 1;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-header .close:before, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-header .close:before {
     border-style: solid;
     border-color: #007bff;
     border-width: 0.15em 0.15em 0 0;
     content: '';
     display: inline-block;
     height: 0.5em;
     left: 0.55em;
     position: relative;
     top: 0.28em;
     transform: rotate(-45deg);
     vertical-align: top;
     width: 0.5em;
     transform: rotate(-135deg);
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-footer, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-footer {
     position: absolute;
     top: 0;
     right: 0;
     padding: 0;
     margin: 0 10px 0 0;
     height: 50px;
     min-height: 50px;
     line-height: 50px;
     background-color: white;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-footer .btn, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-footer .btn {
     border: none;
     background-color: transparent;
     margin: 0 0 0 10px;
     padding: 0;
     outline: none;
     box-shadow: none;
     font-size: 1.0rem;
     color: #007bff;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-footer .btn:hover, .modal.modal-fullscreen .modal-dialog .modal-content .modal-footer .btn:active, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-footer .btn:hover, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-footer .btn:active {
     text-decoration: underline;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-footer .btn[data-dismiss="modal"], .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-footer .btn[data-dismiss="modal"] {
     display: none;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-footer .material-icons, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-footer .material-icons {
     vertical-align: middle;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-footer .fa, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-footer .fa {
     vertical-align: middle;
     font-size: 1.25em;
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-body, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-body {
     flex-shrink: 3;
     overflow: auto;
     -webkit-overflow-scrolling: touch;
   }
   .modal.modal-fullscreen-bottom-footer .modal-dialog .modal-content .modal-footer, .modal-fullscreen-bottom-footer .modal .modal-dialog .modal-content .modal-footer {
     position: inherit;
     top: inherit;
     right: inherit;
     padding: 15px;
     margin: 0;
   }
   .modal.modal-fullscreen-bottom-footer .modal-dialog .modal-content .modal-footer .btn, .modal-fullscreen-bottom-footer .modal .modal-dialog .modal-content .modal-footer .btn {
     margin: 0 0 0 18px;
   }
 }
 @media (min-width: 768px) {
   .modal.modal-fullscreen .modal-dialog .modal-content, .bootstrap-fs-modal .modal .modal-dialog .modal-content {
     max-height: calc(100vh - 60px);
   }
   .modal.modal-fullscreen .modal-dialog .modal-content .modal-body, .bootstrap-fs-modal .modal .modal-dialog .modal-content .modal-body {
     flex-shrink: 3;
     overflow: auto;
     -webkit-overflow-scrolling: touch;
   }
 }

</style>


