/**
* Created by vouill on 11/13/17.
*/


<template>
  <div>
    <loading v-if="loading"/>
    <div v-if="isAuthenticated">
      <div id="wrapper" class="toggled">

         <!-- Sidebar -->
         <div id="sidebar-wrapper">
             <ul class="sidebar-nav">
                 <li class="sidebar-brand">
                     <a href="#">
                         Bootstrap
                     </a>
                 </li>
                 <li>
                 <navigation></navigation>
                 </li>
                 <li v-bind:class="{'active': selectedlabel == 'All Labels', '': !(selectedlabel == !'All Labels')}" >
                     <a @click="allLabels" href="#" >All Labels</a>
                   </li>
                 <li v-bind:class="{'active': selectedlabel == label.label, '': !(selectedlabel == !(label.label))}" v-for="label in labels">
                     <a @click="clickedLabel(label.label)" href="#">{{ label.label }}</a>
                 </li>
                 
             
             </ul>
         </div>
         <!-- /#sidebar-wrapper -->
   
         <!-- Page Content -->
         <div id="page-content-wrapper">
               
             <div class="container-fluid">
                  
                 <a @click="changeMenu" href="#menu-toggle" class="btn btn-secondary" id="menu-toggle">Toggle Menu</a>
                 <cards></cards>
             </div>
         </div>
 
 
     </div>

             <!-- <cards></cards> -->
      
    </div>
    <div v-if="!isAuthenticated && authStatus !== 'loading'">
      <login/>
    </div>
  </div>
</template>



<script>
  import fakeFeed from './fakeFeed'
  import FeedItem from './feedItem.vue'
  import contact from './contact.vue'
  import cards from './cards.vue'
  import { mapGetters } from 'vuex'
  import Login from 'components/login'
  import Navigation from 'components/navigation'
  import axios from 'axios';


  export default {
    components: {
      Login,
      FeedItem,
      contact,
      cards,
      Navigation
    },
    name: 'home',
    computed: {
      ...mapGetters(['isAuthenticated', 'authStatus']),
      loading: function () {
        return this.authStatus === 'loading' && !this.isAuthenticated
      }
    },
    data () {
      return {
          fakeFeed,
          labels: [],
          selectedlabel: "" 
          
          }
    },
    created(){
       this.fetchLabels();

    },
    mounted(){

       let vm = this;
         this.$root.$on('refreshlabel', () => {
            vm.fetchLabels();
         })

         

    },
    methods:{
       changeMenu(){
         $("#wrapper").toggleClass("toggled");
       },
       fetchLabels(){
         axios
            .get('/getuniquelabels')
            
            .then(res => {
               
               this.labels = res.data;
               // console.log(this.labels);
            })
            .catch(err => console.log(err));

       },
       allLabels(){
       
         this.selectedlabel = 'All Labels';
          this.$root.$emit('thelabel', 'All Labels');

       },
       clickedLabel(thelabel){
            // $('li.active').removeClass('active');
            // $('#items li').closest('li').addClass('active');
         this.selectedlabel = thelabel;
          this.$root.$emit('thelabel', thelabel);

       }
    }
  }
</script>
<style>
      /*!
    * Start Bootstrap - Simple Sidebar (https://startbootstrap.com/template-overviews/simple-sidebar)
    * Copyright 2013-2017 Start Bootstrap
    * Licensed under MIT (https://github.com/BlackrockDigital/startbootstrap-simple-sidebar/blob/master/LICENSE)
    */
   li.active{
      background-color: #696969;
   }
   
   body {
     overflow-x: hidden;
   }
   
   #wrapper {
     padding-left: 0;
     -webkit-transition: all 0.5s ease;
     -moz-transition: all 0.5s ease;
     -o-transition: all 0.5s ease;
     transition: all 0.5s ease;
   }
   
   #wrapper.toggled {
     padding-left: 250px;
   }
   
   #sidebar-wrapper {
     z-index: 1000;
     position: fixed;
     left: 250px;
     width: 0;
     height: 100%;
     margin-left: -250px;
     overflow-y: auto;
     background: #000;
     -webkit-transition: all 0.5s ease;
     -moz-transition: all 0.5s ease;
     -o-transition: all 0.5s ease;
     transition: all 0.5s ease;
   }
   
   #wrapper.toggled #sidebar-wrapper {
     width: 250px;
   }
   
   #page-content-wrapper {
     width: 100%;
     position: absolute;
     padding: 15px;
   }
   
   #wrapper.toggled #page-content-wrapper {
     position: absolute;
     margin-right: -250px;
   }
   
   
   /* Sidebar Styles */
   
   .sidebar-nav {
     position: absolute;
     top: 0;
     width: 250px;
     margin: 0;
     padding: 0;
     list-style: none;
   }
   
   .sidebar-nav li {
     text-indent: 20px;
     line-height: 40px;
   }
   
   .sidebar-nav li a {
     display: block;
     text-decoration: none;
     color: #999999;
   }
   
   .sidebar-nav li a:hover {
     text-decoration: none;
     color: #fff;
     background: rgba(255, 255, 255, 0.2);
   }
   
   .sidebar-nav li a:active, .sidebar-nav li a:focus {
     text-decoration: none;
   }
   
   .sidebar-nav>.sidebar-brand {
     height: 65px;
     font-size: 18px;
     line-height: 60px;
   }
   
   .sidebar-nav>.sidebar-brand a {
     color: #999999;
   }
   
   .sidebar-nav>.sidebar-brand a:hover {
     color: #fff;
     background: none;
   }
   
   @media(min-width:768px) {
     #wrapper {
       padding-left: 0;
     }
     #wrapper.toggled {
       padding-left: 250px;
     }
     #sidebar-wrapper {
       width: 0;
     }
     #wrapper.toggled #sidebar-wrapper {
       width: 250px;
     }
     #page-content-wrapper {
       padding: 20px;
       position: relative;
     }
     #wrapper.toggled #page-content-wrapper {
       position: relative;
       margin-right: 0;
     }
   }
   
   
   </style>


